<script>

import { supabase } from '../supabase/supabase.js'
import { onMounted, ref, toRefs } from 'vue'
import store from "../store/index.js"
import {useRouter} from 'vue-router'
import Header from '@/components/Header.vue'

export default {
  name: 'Homepage',
  components: {
    Header
  },
  setup(){
    const router = useRouter();
   
    onMounted(()=>{

      if (screen.width < 1024){
        router.push({ path: '/mobile' })
      }
      
      if(store.session.value.data.session!=null){
        router.push({ path: '/lab' })
      }
    })

    async function LoginWithGoogle(){
      const { data, error } = await supabase.auth.signInWithOAuth({
        provider: 'google'
      })

    }

    async function LoginWithNotion(){
      const { data, error } = await supabase.auth.signInWithOAuth({
        provider: 'notion'
      })

    }

    function redirect(){
      router.push({ path: '/about' })
    }

     function twitter(){
      window.open("https://twitter.com/getbrainbox", '_blank');
     } 
    

    
    return {LoginWithGoogle,LoginWithNotion,redirect,twitter}



    


  }
}
</script>

