import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import DashboardView from '../views/DashboardView.vue'
import AboutUsView from '../views/AboutUsView.vue'
import HelpView from '../views/HelpView.vue'
import MobileScreenView from '../views/MobileScreenView.vue'
import ErrorView from '../views/ErrorView.vue'
import { supabase } from '../supabase/supabase.js'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta:{
      auth: false,
    },
  },
  {
    path: '/lab',
    name: 'lab',
    component: DashboardView,
    meta:{
      auth: true,
    },},
    {
      path: '/about',
      name: 'about',
      component: AboutUsView,
  },
  {
    path: '/help',
    name: 'help',
    component: HelpView,
},
{
  path: '/mobile',
  name: 'mobile',
  component: MobileScreenView
},
  { path: '/:pathMatch(.*)*', name: 'error', component: ErrorView }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach(async(to, from, next) => {

  //redirects user on a mobile screen

  

  // get current user info
  
  const currentUser = await supabase.auth.getSession();
 
  const requiresAuth = to.matched.some(record => record.meta.auth);
 
  if(requiresAuth && currentUser['data']['session']==null) next('/');
  else if(!requiresAuth && currentUser) next();
  else next();



})

export default router
