<template>

<Header/>

<div class="hero min-h-screen bg-white">
  <div class="hero-content flex-col lg:flex-row-reverse">
  <img src="../assets/undraw_online_connection_6778.svg" class="max-w-sm rounded-lg" />

    <div>
      <h1 class="text-5xl font-bold">Hi there</h1>
      <p class="py-6 text-base">
      <b>BrainBox</b> is built on the idea that your digital brain should be your most valuable asset. By allowing you to easily save and organize URLs, it helps you to build a comprehensive and personalized knowledge base that you can access whenever you need it. Whether you're a student, a researcher, a professional, or simply a curious learner, it is the perfect solution for you.
      </p>
      
    </div>
  </div>
</div>

</template>


<script>
import {useRouter} from 'vue-router'
import Header from '@/components/Header.vue'
import {onMounted,ref} from 'vue'
export default {
    name:"AboutUS",
    components:{Header},

    setup(){
      const router = useRouter();

      onMounted(()=>{
        if (screen.width < 1024){
          router.push({ path: '/mobile' })
        }
      })

      function redirect(path){
        
        router.push({ path: `/${path}` })
      }

      function twitter(){
        window.open("https://twitter.com/getbrainbox", '_blank');
       } 
      return {redirect,twitter}
    }
  }
</script>