<template>
<div class="min-h-full bg-white-100">
  <div class="flex flex-col">
    <!-- Header -->
    <header id="hope" class="flex justify-between items-center bg-white py-4 px-6">
<div class="flex items-center">
<!--         <img src="https://img.logoipsum.com/293.svg" alt="Logo" class="h-8 mr-2"> -->
        <h1 class="text-2xl font-bold text-gray-800">BrainBox</h1>
      </div>
      
 <div v-show="showurl" class="flex items-center">     
        <div class="relative mr-4 flex flex-row space-x-2">
          <input type="url" id="URL" placeholder="Save a URL" v-model="url" class="py-2 px-4 pr-12 bg-gray-200 rounded-md focus:outline-none focus:bg-white focus:border-sky-300 focus:border-2">
          <button @click="add_URL" class="bg-blue-500 hover:bg-blue-600 text-white rounded-md py-2 px-4">Add</button>
        </div>
   <button @click="toggle_url_bar" class="text-gray-400 hover:text-gray-600 focus:outline-none">
    <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z"/><line x1="4" y1="4" x2="20" y2="20" /><line x1="20" y1="4" x2="4" y2="20" /></svg>
   </button>
   </div>

   <div v-show="show_search_bar" class="flex items-center">     
        <div class="relative mr-4 flex flex-row space-x-2">
        <input type="url" id="URL" placeholder="Search for saved URLs" v-model="search" class="py-2 px-4 pr-12 bg-gray-200 rounded-md focus:outline-none focus:bg-white focus:border-sky-300 focus:border-2">
        <button @click="search_URLs(search)" class="bg-blue-500 hover:bg-blue-600 text-white rounded-md py-2 px-4">Search</button>
        </div>
   <button @click="toggle_search_bar" class="text-gray-400 hover:text-gray-600 focus:outline-none">
    <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z"/><line x1="4" y1="4" x2="20" y2="20" /><line x1="20" y1="4" x2="4" y2="20" /></svg>
   </button>
   </div>
      
      <div class = "flex flex-row items-center space-x-4">
      <div v-show="!showurl" class="tooltip tooltip-left" data-tip="Save a URL">
   <button @click="toggle_url_bar" v-show="show_header_menu" class="w-6 h-6 flex justify-center items-center text-gray-400 hover:text-gray-500">
      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
         <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 4v16m8-8H4"></path>
      </svg>
      
        </button>
        </div>
        
        <div v-show="!show_search_bar" class="tooltip tooltip-left" data-tip="Search for URLs">
        <button @click="toggle_search_bar" v-show="show_search_menu" class="w-6 h-6 flex justify-center items-center text-gray-400 hover:text-gray-500">
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path d="m15.97 17.031c-1.479 1.238-3.384 1.985-5.461 1.985-4.697 0-8.509-3.812-8.509-8.508s3.812-8.508 8.509-8.508c4.695 0 8.508 3.812 8.508 8.508 0 2.078-.747 3.984-1.985 5.461l4.749 4.75c.146.146.219.338.219.531 0 .587-.537.75-.75.75-.192 0-.384-.073-.531-.22zm-5.461-13.53c-3.868 0-7.007 3.14-7.007 7.007s3.139 7.007 7.007 7.007c3.866 0 7.007-3.14 7.007-7.007s-3.141-7.007-7.007-7.007z" fill-rule="nonzero"/>
          </svg>
          
        </button>
        </div>
        

     

<div class="dropdown dropdown-left dropdown-bottom">
   <img :src="user_img" alt="Logo" class="h-10 rounded-full border-2" tabindex="0">
   <ul tabindex="0" class="dropdown-content menu p-2 shadow bg-base-100 rounded-box w-52 flex flex-col space-y-2">
      <li><button @click="redirect('help')" class="btn glass"><span class="text-black">Help</span></button></li>
      <li><button @click="logoutUser" class="btn glass"><span class="text-black">Logout</span></button></li>
   </ul>
</div>

      </div>
    </header>
    
    <!-- Main Content -->
    <div class="flex-1 flex">
  <SideBar :create_folder="create_folder" :folder_json="folder_json" @show_url_menu="toggle_menu"
  @track_folder="track_deleted_folders"/>
      
  <Links :show="show_root_url" :toggle="toggle_subURL" :links_json="links_json" :selected_root_url="selected_root_url" @track_links="track_del_links"/>

  
  <SubURLS :tags="tags" :show="show_subURL" :suburl_json="suburl_json" @update_suburls='update_suburls' @search_from_tags="search_from_tags"/>

      

    </div>
  
  </div>
</div>

</template>

<script>

import { supabase } from '../supabase/supabase.js'
import {useRouter} from 'vue-router'
import {onMounted,ref} from 'vue'
import store from "../store/index.js"
import SideBar from "./SideBar.vue"
import Links from "./Links.vue"
import SubURLS from "./SubURLS.vue"



export default {
  name: 'Dashboard',
  components:{SideBar,Links,SubURLS},

  setup(){
    const user_img = store.session.value.data.session.user.user_metadata.avatar_url
    const user_email = store.session.value.data.session.user.email
    const router = useRouter();
    const showurl = ref(false)
    const show_subURL = ref(false)
    const selected_rootURL = ref(null)
    const folder_json = ref([])
    const show_header_menu = ref(false)
    const number_of_folders = ref(null)
    const current_folder = ref(null)
    const url = ref(null)
    const regex = '[-a-zA-Z0-9@:%_+.~#?&//=]{2,256}(.[a-z]{2,4})?\b(/[-a-zA-Z0-9@:%_+.~#?&//=]*)?'
    const isdisabled = ref(false)
    const links_json = ref([])
    const selected_root_url = ref(0)
    const show_root_url = ref(true)
    const suburl_json = ref([])
    const show_search_bar = ref(false)
    const show_search_menu = ref(true)
    const search = ref(null)
    const tags= ref([])

    const keyword_extractor = require("keyword-extractor");

    onMounted(async ()=>{
      const { data, error1 } = await supabase.from('Folders').select().eq('email',user_email)
      folder_json.value = data
      number_of_folders.value = data.length

      if (screen.width < 1024){
        router.push({ path: '/mobile' })
      }
      
      
      if(number_of_folders.value<1){
        show_header_menu.value=false
        show_search_menu.value = false
        }
        else{
          toggle_menu(data[0].id)
          selected_root_url.value = data[0].id


          
        }
       
    })

    function fetchMetaData(url) {
      return fetch(`https://api.dub.sh/metatags?url=${url}`,{headers: { "Access-Control-Allow-Origin": "*",
      'Referer': 'https://api.dub.sh/'}})
        .then(response => response.json())
        .then((data) => {
          
          const title = data['title'];
          const description = data['description'];
          const ogImage = data['image']
          return { title, description, ogImage };
        })
        .catch(error => {
          console.error(error);
          return { title: '', description: '', ogImage: '' };
        });
    }
    
    
    async function logoutUser(){
      const { error } = await supabase.auth.signOut()
      router.push({ path: '/' })
    }

    function toggle_url_bar(){
      showurl.value = !showurl.value
      show_search_bar.value = false
      url.value = ''
    }

   async function toggle_subURL(e){
      show_subURL.value = true
      selected_root_url.value = e
      store.current_root_url_id.value = e

      const { data, error } = await supabase.from('SubURLs').select().eq('URLid',e)
      suburl_json.value = data

      const current_url_id = store.current_root_url_id.value
      
      const { data:data2, error:error2 } = await supabase.from('URLs').select('tags').eq('id',store.current_root_url_id.value)
      tags.value = data2[0]['tags']['tags']
      
      
    } 

    async function toggle_menu(show){
      show_header_menu.value = true
      show_subURL.value = false
      current_folder.value = show
      

      const { data, error } = await supabase
    .from('URLs')
    .select('URL, title,description,img,id')
    .eq('folder_id', current_folder.value)
    .order('id', { ascending: false })

    links_json.value = data
    show_root_url.value = true
    suburl_json.value = null

    }


    async function track_deleted_folders() {
      
      show_header_menu.value=false
      showurl.value = false
      show_search_bar.value = false
      show_subURL.value = false
      show_root_url.value = false
      const { data, error1 } = await supabase.from('Folders').select().eq('email',user_email)
      folder_json.value = data
      suburl_json.value = null
      if(data.length<1){show_search_menu.value = false}
    }

    async function track_del_links(){
      show_subURL.value = false
      

      const { data, error } = await supabase
      .from('URLs')
      .select('URL, title,description,img,id')
      .eq('folder_id', current_folder.value)
      .order('id', { ascending: false })

      links_json.value = data
      suburl_json.value = null
    }

    async function create_folder() {
    const { error } = await supabase.from('Folders').insert({ email: user_email,folder_name:"New Folder" })
    const { data, error1 } = await supabase.from('Folders').select().eq('email',user_email)
    folder_json.value = data
    show_search_menu.value = true 
    }

    async function add_URL(){
      isdisabled.value = !isdisabled.value
      const res =  url.value.match(/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g)
      if(res != null){

        fetchMetaData(url.value)
        .then(async metadata => {
          const tags = {"tags":keyword_extractor.extract(metadata.description,{remove_duplicates:true})}
          const {data,error } = await supabase
          .from('URLs')
          .insert({ email: user_email, folder_id:current_folder.value,URL: url.value,
          title:metadata.title,description:metadata.description,img:metadata.ogImage,
        tags: tags}).select()
          
          links_json.value.unshift(data[0])

          isdisabled.value = !isdisabled.value
          url.value = null
        })
        .catch(error => {console.error(error);isdisabled.value = !isdisabled.value});
      }
    }

    async function update_suburls(){
      const { data, error1 } = await supabase.from('SubURLs').select().eq('URLid',store.current_root_url_id.value)
      suburl_json.value = data
    }

    function toggle_search_bar(){
      showurl.value = false
      show_search_bar.value = !show_search_bar.value
      
    }

   async function search_URLs(val){
      
      const {data,error} = await supabase.
      from('URLs').select().textSearch('description',val,{
        type:"websearch",config:"english",
      })
      links_json.value = data
      show_subURL.value = false
    }

    async function search_from_tags(tag){
      toggle_search_bar()
      search_URLs(tag)
      search.value = tag
    }

    function redirect(path){
      
      router.push({ path: `/${path}` })
    }


    return {logoutUser,user_img,toggle_url_bar,showurl,show_subURL,toggle_subURL,create_folder
      ,folder_json,show_header_menu,toggle_menu,track_deleted_folders,add_URL,url,isdisabled,
      links_json,selected_root_url,track_del_links,show_root_url,suburl_json,update_suburls,
      toggle_search_bar,search_URLs,show_search_bar,search,show_search_menu,tags,search_from_tags,
      redirect}
  }
}
</script>