    To get started with BrainBox, follow these steps:
<ul class="flex flex-col space-y-2 list-decimal pl-4">
   
    <li class="mt-2">
Sign up for an account: To use BrainBox, you'll need to sign up for an account. You can do this either by logging with Google or Notion.
</li>
<li>
Create a folder: Once you've signed up, you can create a folder to organize your URLs. To do this, click the "New Folder" button and give your folder a name. You can create as many folders as you need to keep your URLs organized.
</li>
<li>
Save a root URL: To save a root URL, simply copy the URL of the website you want to save and paste it into the "Add URL" field. Then, give your root URL a name and select the folder where you want to save it. 
</li>
<li>
Map sub-URLs: Once you've saved a root URL, you can map sub-URLs to it by clicking the "Map Sub-URLs" button. This will bring up a new window where you can enter the sub-URLs you want to map to the root URL. You can map as many sub-URLs as you need to organize your resources.
</li>
</ul>


