<template>
<div class="navbar bg-white">
  <div class="flex-1">
    <a @click="redirect('')" class="hover:cursor-pointer font-bold normal-case text-xl">BrainBox</a>
  </div>
  <div class="flex-none">
    <ul class="menu menu-horizontal px-1 space-x-4">
      <li>
        <button @click="redirect('about')" class="btn btn-ghost">About Us</button></li>

        <li><button @click="redirect('help')" class="btn btn-ghost">Help</button></li>
      <li><button href="https://twitter.com/getbrainbox" @click="twitter" target="_blank" class="btn btn-ghost">Twitter</button></li>
    </ul>
  </div>
</div>
</template>


<script>
import {useRouter} from 'vue-router'
export default {
    name:"Header",
    setup(){
        const router = useRouter();
        function redirect(path){
            router.push({ path: `/${path}` })
          }
      
           function twitter(){
            window.open("https://twitter.com/getbrainbox", '_blank');
           } 
           return {redirect,twitter}
    }}
</script>