<template>

<!-- Sidebar -->
      <div class="w-1/4 bg-white shadow-md min-h-screen">
        <ul class="">
          <li class="px-6 py-4 flex justify-between items-center">
            <span class="font-semibold">Folders:</span>
            <div class="tooltip tooltip-left" data-tip="Create a new folder">
            <button @click="create_folder" class="w-6 h-6 flex justify-center items-center text-gray-400 hover:text-gray-500">
              
              <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 4v16m8-8H4"></path>
              </svg>
            </button>
            </div>
          </li>
          </ul>
          <div v-show="folder_json.length==0" class="flex flex-col space-y-10 ml-4 text-md justify-center items-center">
            <img src="../assets/void.svg" class="w-32">
              
              <div class="text-xs">Organize your URLs easily with folders.</div>
              <div class="text-xs">Create a 'New Folder' to start!</div>
            </div>
          <ul class="divide-y divide-gray-200">

<li  class="px-6 py-4 flex justify-between items-center hover:bg-gray-50" v-for="d in folder_json" :key="d.id" :id="d.id" :class="{'bg-sky-100':d.id==current_folder}">
  <button @click="update_current_folder"  class="inline-block truncate float-left">
           <span @dblclick="edit_folder_name" @blur="update_folder_name">{{d.folder_name}}</span> 
            </button>
    
  <button v-show="d.id==current_folder" @click="delete_folder" class="w-6 h-6 flex justify-center items-center hover:text-red-500 float-right">       
  <div  class="tooltip tooltip-left" data-tip="Delete">
  🗑️
  </div>
  </button>

</li>


        </ul>
      </div>

</template>

<script>
import { ref,defineComponent  } from 'vue'
import { supabase } from '../supabase/supabase.js'


export default {
    name: 'SideBar',
    props: ['create_folder','folder_json'],
    setup(props,context,folder_empty_state){

      const current_folder = ref(null)
      const delete_folder_id = ref(null)
      
      
      function update_current_folder(e){
        current_folder.value = e.target.parentElement.parentElement.id
        context.emit('show_url_menu',current_folder.value)
     
        
      }

      async function delete_folder(e){
        delete_folder_id.value = e.target.parentElement.parentElement.id

        const { data,error } = await supabase
      .from('Folders')
      .delete()
      .eq('id', delete_folder_id.value)

      
      const { data2,error2 } = await supabase
      .from('URLs')
      .delete()
      .eq('folder_id', delete_folder_id.value)


      
      context.emit('track_folder')
      }

      function edit_folder_name(e){
        e.target.setAttribute('contenteditable','true')
        
      }

      async function update_folder_name(e){
        e.target.removeAttribute('contenteditable')
        update_current_folder(e)
        const folder_id = e.target.parentElement.parentElement.id
        const new_name = e.target.innerHTML
        
        const { data,error } = await supabase.from('Folders')
        .update({ folder_name: new_name }).eq('id', folder_id)

      }

      return {update_current_folder,current_folder,delete_folder,edit_folder_name,update_folder_name}

    }}

</script>