<template>
<!-- Links -->
<div v-show="show" class="w-1/3 bg-white shadow-md min-h-screen">

<div v-show="links_json.length==0" class="text-xs flex justify-center mt-4">
Add a URL to get started.
</div>


        <ul class="divide-y divide-gray-200">
          <div v-for="l in links_json" :key="l.id" >
          <li :id="l.id"
          class="flex flex-col px-6 py-4 hover:bg-gray-50 " :class="{'bg-gray-100':l.id==selected_root_url}">
            
            <div @click="toggle(l.id)" class="flex flex-row justify-between items-center">
              <img :src="l.img==null ? default_img : l.img" alt="..." class=" drop-shadow-md object-contain h-32 w-32 flex flex-end border-none" />
                <div class="flex-1 truncate ml-2">
                  <span class="font-semibold">{{l.title}}</span>
                 <div class="mt-2 truncate text-gray-500 text-sm">
                  <span class="text-gray-500 text-sm">{{l.description}}</span> 
                 </div>
                  <div class="mt-2 truncate text-gray-500 text-sm">
                  <span class="text-gray-500 text-sm">{{l.URL}}</span> 
                 </div>
                </div>
            </div>
            <div  class="flex flex-row space-x-2 text-xs text-gray-500 pt-2">
              <a :href="l.URL" target="_blank">View Original</a>
              <button v-show="l.id==selected_root_url" class="text-red-500" @click="delete_link">Delete</button>
            </div>
            
          </li>
          </div>
        </ul>
      </div>
</template>

<script>
import store from "../store/index.js"
import { ref,defineComponent  } from 'vue'
import { supabase } from '../supabase/supabase.js'

    export default{
        name:'Links',
        props: ['toggle','links_json','selected_root_url','show'],
        setup(props,context){

          const default_img = new URL('@/assets/undraw_outer_space_re_u9vd.svg',import.meta.url)


          

          async function delete_link(e){
            const current_root_id = e.target.parentElement.parentElement.id
            

            const { data,error } = await supabase
            .from('URLs')
            .delete()
            .eq('id', current_root_id)

            const { data2,error2 } = await supabase
            .from('SubURLs')
            .delete()
            .eq('URLid', current_root_id)
            context.emit('track_links')

          }

          return {delete_link,default_img}
          
        }
    }
</script>