<template>
<!-- SubURLS -->
      <div v-show="show" class=" shadow-md flex-1 flex-col bg-white overflow-y-auto scrollbar-hidden">
       
         <div class="flex flex-col items-center">     
        <div class="relative mr-4 flex flex-row space-x-2">
          <input type="text" placeholder="Save a sub URL" v-model="suburl_input" class="py-2 px-4 pr-12 bg-gray-200 rounded-md focus:outline-none focus:bg-white focus:border-amber-300 focus:border-2">
          <button @click="create_sub_url(current_url_id)" class="bg-amber-500 hover:bg-amber-600 text-white rounded-md py-2 px-4">Add</button>
        </div>
        </div>
        
        <div v-if="tags.length>0" class="flex flex-row justify-center mt-2 space-x-2 space-y-2 flex-wrap">
          <span></span>
        <span @click="search_from_tags(tag)" v-for="(tag, index) in tags" :id=index class="bg-sky-100 text-blue-800 text-xs font-medium mr-2 px-2.5 py-0.5 rounded dark:bg-blue-900 dark:text-blue-300 hover:cursor-pointer">{{tag}}</span>

        </div>


        
        <div class="px-6 py-4">

            <ul class="divide-y divide-gray-200 border-dashed border-gray-300" :class="{'border-2':show_border(suburl_json)}">
          <li v-for="s in suburl_json" :key="s.id" :id="s.id" class="flex flex-col bg-white px-6 py-4 hover:bg-gray-50">
            <div class="flex flex-row justify-between items-center ">
              <img :src="s.img==null ? default_img : s.img" alt="..." class="rounded object-scale-down h-24 w-24 flex flex-end border-none" />
                <div class="flex-1 truncate ml-2">
                  <span class="font-semibold">{{s.title}}</span>
                 <div class="mt-2 truncate text-gray-500 text-sm">
                  <span class="text-gray-500 text-sm">{{s.description}}</span> 
                 </div>
                  <div class="mt-2 truncate text-gray-500 text-sm">
                  <span class="text-gray-500 text-sm">{{s.URL}}</span> 
                 </div>
                </div>
            </div>
            <div class="flex flex-row space-x-2 text-xs text-gray-500 pt-4">
              <a :href="s.URL" target='_blank'>View Original</a>
              <button @click="delete_suburl" class="text-red-500">Delete</button>
            </div>
          </li>


        </ul>
        </div>
      </div>
</template>

<script>
import {ref,onMounted} from 'vue'
import store from "../store/index.js"
import { supabase } from '../supabase/supabase.js'

    export default{
        name:'SubURLs',
        props: ['show','suburl_json','tags','links_json'],
        setup(props,context){

          const default_img = new URL('@/assets/undraw_outer_space_re_u9vd.svg',import.meta.url)
          const regex = '[-a-zA-Z0-9@:%_+.~#?&//=]{2,256}(.[a-z]{2,4})?\b(/[-a-zA-Z0-9@:%_+.~#?&//=]*)?'
          const user_email = store.session.value.data.session.user.email
          const suburl_input = ref(null)


          function fetchMetaData(url) {
            return fetch(`https://api.dub.sh/metatags?url=${url}`,{headers: { "Access-Control-Allow-Origin": "*",
            'Referer': 'https://api.dub.sh/' }})
              .then(response => response.json())
              .then((data) => {
                
                const title = data['title'];
                const description = data['description'];
                const ogImage = data['image']
                return { title, description, ogImage };
              })
              .catch(error => {
                console.error(error);
                return { title: '', description: '', ogImage: '' };
              });
          }

          function create_sub_url(m) {
            
            const current_url_id = store.current_root_url_id.value
            
            const res =  suburl_input.value.match(/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g)
            if(res != null){
              fetchMetaData(suburl_input.value).then(
                async metadata =>{
                  
                  const {data,error } = await supabase
                  .from('SubURLs')
                  .insert({ email: user_email, URLid:current_url_id,URL: suburl_input.value,
                  title:metadata.title,description:metadata.description,img:metadata.ogImage}).select()
                  context.emit('update_suburls')
                  suburl_input.value = null
                }
              )
            }
          }

          async function delete_suburl(e){
            const current_suburl_id = e.target.parentElement.parentElement.id
            const { data,error } = await supabase
            .from('SubURLs')
            .delete()
            .eq('id', current_suburl_id)

            context.emit('update_suburls')

          }

          function search_from_tags(tag){
            
            context.emit('search_from_tags',tag)
          }

          function show_border(m){
            
            return typeof(m[0])!== "undefined"
          }
          
          return{suburl_input,create_sub_url,delete_suburl,search_from_tags,show_border,default_img}
          
        }
    }
    </script>